import React from 'react';
import { NavLink } from 'react-router-dom';
import Button from 'Button';

import './Header.css';

export default class Header extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.state = {
      menuOpen: false
    }
  }

  handleClick(value) {
    const menuEl = window.document.getElementById('hamburger-menu');
    if (this.state.menuOpen) {
      menuEl.style.visibility = 'hidden';
      menuEl.style.opacity = 0;
      this.setState({ menuOpen: false });
    } else {
      menuEl.style.visibility = 'visible';
      menuEl.style.opacity = 1;
      this.setState({ menuOpen: true });
    }
  }

  emailLink() {
    const coded = "0mLY@Am7jpo8p.Pp0.m3";
    const key = "DoqmzkXAEwiULB8CsTuGxpb0WYyKOMdZVtR1Qc3n94Nleav5hgjI2FrSfPJ76H"
    let shift = coded.length
    let link = ""
    let ltr = ""
    for (let i = 0; i < coded.length; i++) {
      if (key.indexOf(coded.charAt(i)) === -1) {
        ltr = coded.charAt(i)
        link += (ltr)
      } else {
        ltr = (key.indexOf(coded.charAt(i)) - shift + key.length) % key.length
        link += (key.charAt(ltr))
      }
    }
    return `mailto:${link}`
  }

  render() {
    return (
      <header className="padding-0 col-md-12">
        <nav>
          <div className="row">
            <div className="col-8">
              <ul className="tabs">
                <li className="tab"><NavLink to="/" className="home-btn"><i className="fas fa-home" /></NavLink></li>
                {/* <li className="tab"><NavLink activeClassName='is-active' to="/resume">RESUME</NavLink></li> */}
                <li className="tab"><NavLink activeClassName='is-active' to="/blog">BLOG</NavLink></li>
                {/* <li className="tab"><NavLink activeClassName='is-active' to="/portfolio">PORTFOLIO</NavLink></li> */}
                <li className="tab"><NavLink activeClassName='is-active' to="/resume">RESUME</NavLink></li>
                {/* <li className="tab"><NavLink activeClassName='is-active' to="/contact">CONTACT</NavLink></li> */}
              </ul>
            </div>
            <div className="col-4">
              <Button href={this.emailLink()} className="d-none d-lg-block bottom_30 icon float-right">
                Hire Me
                  <i className="fas fa-paper-plane" />
              </Button>

              <a className="hamburger float-right d-block d-md-none" onClick={this.handleClick}>
                <i className="fa fa-bars" aria-hidden="true"></i>
              </a>
              <div className="d-none d-md-block social-icons float-right">
                <a className="tw" href="https://www.linkedin.com/in/magandolfo/"><i className="fab fa-linkedin-in"></i></a>
                <a className="gh" href="https://github.com/markgandolfo"><i className="fab fa-github"></i></a>
                <a className="ins" href="https://twitter.com/markgandolfo"><i className="fab fa-twitter"></i></a>
              </div>
            </div>
          </div>
          <div id="hamburger-menu" className="menuout">
            <div className="menuin">
              <ul className="tabs">
                <li className="tab"><NavLink to="/" className="home-btn"><i className="fas fa-home" /></NavLink></li>
                {/* <li className="tab"><NavLink activeClassName='is-active' to="/resume">RESUME</NavLink></li> */}
                <li className="tab"><NavLink activeClassName='is-active' to="/blog">BLOG</NavLink></li>
                {/* <li className="tab"><NavLink activeClassName='is-active' to="/portfolio">PORTFOLIO</NavLink></li> */}
                <li className="tab"><NavLink activeClassName='is-active' to="/resume">RESUME</NavLink></li>
                {/* <li className="tab"><NavLink activeClassName='is-active' to="/contact">CONTACT</NavLink></li> */}
              </ul>
            </div>
          </div>
        </nav>
      </header>
    );
  }
}



