import React, { Component } from 'react';
import PropTypes from 'prop-types'

import Showdown from 'showdown';
import TagList from './TagList';
import Footer from 'Footer';
import ArticleHeader from './ArticleHeader';

import './Article.css'

const converter = new Showdown.Converter();

export default class Article extends Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
    body: PropTypes.string.isRequired,
    sidebar: PropTypes.any,
    tagList: PropTypes.string
  }

  componentDidMount() {
    window.Prism.highlightAll();
  }

  componentDidUpdate() {
    window.Prism.highlightAll();
  }

  tagsComponent() {
    if (this.props.tagList && this.props.tagList.length > 0) {
      return (<TagList tagList={this.props.tagList} />)
    }
  }

  render() {
    const body = { __html: converter.makeHtml(this.props.body) }
    return (
      <div id="article">
        <ArticleHeader />

        <div id="content">
          <div className="row">
            <div className="col-md-9 post">
              <div className="post-content">
                <h1 className="blog-title bottom_30 top_30">{this.props.title}</h1>
                <div dangerouslySetInnerHTML={body} />
              </div>

              {this.tagsComponent()}
            </div>

            <div className="col-md-3">
              {this.props.sidebar}
            </div>
          </div>
        </div>
        <Footer />

      </div>
      )
    }
  }



