module.exports = [{"slug":"eljs---a-lightweight-vanilla-javascript-element-creation-library","title":"el.js - A lightweight vanilla javascript element creation library","date":"2013-10-07 22:44","description":"Simple vanilla javascript library used to create dom elements, no other libraries requried.","category":"software development","body":"\n\nSimple Vanilla Javascript library used to create elements, no other libraries required\nFind it on github at the [el.js repository](https://github.com/markgandolfo/el.js)\n\n### Creation of elements\n\nCreate a simple element without attributes\n\n```javascript\nel('div')\n=> <div></div>\n```\n\nSome vanity methods/aliases to help you create elements quickly\n\n```javascript\n# Vanity helpers\nel.div()\nel.p()\nel.input()\nel.img({'src':'http://placekitten.com/200/300'})\nel.a({'src':'http://placekitten.com/200/300'})\n```\n\nFor backwards compatibility you can still access el() via the older create methods\n\n```javascript\nel.create()\nel.c()\n```\n\n### Lets add some content on creation of elements\n\nThere are a few ways to create elements with content inside. The easiest way is to pass it in via the second parameter\n\n```javascript\nel('div', 'Some content')\n=> <div>Some content</div>\n```\n\nIf you pass in a html node as the second attribute, it'll be added as a child\n\n```javascript\nel('ul',\n  el('li', 'item 1')\n)\n=> <ul><li>item 1</li></ul>\n```\n\nIf you want to create a set of li's then you can pass in an array\n\n```javascript\nel('ul', [\n  el('li', 'item 1'),\n  el('li', 'item 2'),\n  el('li', 'item 3'),\n  el('li', 'item 4'),\n  el('li', 'item 5'),\n  el('li', 'item 6')\n  ]\n)\n=> <ul><li>item 1</li><li>item 2</li><li>item 3</li><li>item 4</li><li>item 5</li><li>item 6</li></ul>\n```\n\nFor backwards compatibility you can still add content via 'content'\n\n```javascript\nel('li', {'content':'item 1'})\n```\n\n### Add attributes to your elements\n\nYou could use selectors in the initial name to quick create with id's and classes\n\n```javascript\nel('div#myId.content')\n=> <div id=\"myId\" class=\"content\"></div>\n\nel('div.row')\n=> <div class=\"row\"></div>\n```\n\nel also allows a key:value json object to be passed through as the second attribute\n\n```javascript\nel('div.row', {'id':'myId', 'class':'my_class'})\n=> <div id=\"myId\" class=\"my_class row\"></div>\n\nel('a', {'class':'content', 'href':'https://github.com/markgandolfo/el.js', } 'content' )\n=> <a class=\"content\" href=\"https://github.com/markgandolfo/el.js\">el.js</a>\n```\n\nCreate data-attributes (or any other attributes)\n\n```javascript\nel('div', {'data-action':'submit', 'id':'myId'})\n=> <div data-action=\"submit\" id=\"myId\"></div>\n```\n\n### Callbacks:\n\nYou can now pass a function in as a callback. \n\n```javascript\nel('div', function(done) {\n  console.log('this will be called after the creation of the div');\n})\n```\n\n### Examples of creating elements with id, classes, attributes, content and callbacks\n\n```javascript\nel('ul.navbar', {'data-name':'navigation'}, [\n  el('li', 'item 1'), \n  el('li', 'item 1'), \n  function(element) {\n  this.addEventListener('click', function() {\n    console.log('clicking on the navigation');\n  })\n  }]\n)\n```\n\n## Contributors\n[modsognir](https://github.com/modsognir)\n[thesebas](https://github.com/thesebas)\n"},{"slug":"wget-straight-from-nerdtree-and-vim-using-nerdtree-wgetvim","title":"Wget straight from nerdtree and vim using NERDTree-wget.vim","date":"2013-10-07 22:44","description":"NERDTree plugin to fetch a file from the internet using wget in vim","category":"software development","body":"\nThis is a simple plugin for NERDTree to allow a file to be pulled from the internet\nusing wget. It can be found at the [NERDTree-wget.vi repository](https://github.com/markgandolfo/nerdtree-wget.vim)\n\n## How To Use\nPlace the cursor over the directory you wish to pull the file into, then\nopen the file menu in NERDTree using `m`, then press `w`. You will get an input\nasking you to enter the url of the file. Fill in the the url and press enter.\n\n\n## Requirements\n* Vim 7.2+\n* [NERDTree](https://github.com/scrooloose/nerdtree)\n* wget\n\n## Installation\n\n### Vundle\nTo Install using [Vundle](https://github.com/gmarik/vundle) add the following\nto your vimrc file after the NerdTree Bundle.\n\n```vim\nBundle 'markgandolfo/nerdtree-wget.vim'\n```\n\n### Pathogen\nTo install via [Pathogen](https://github.com/tpope/vim-pathogen) run the\nfollowing commands\n\n```bash\ncd ~/.vim/bundle\ngit clone git@github.com:markgandolfo/nerdtree-wget.vim.git\n```\n"},{"slug":"editableio---a-simple-scratch-pad-to-quikcly-jot-down-your-ideasnotes","title":"editable.io - a simple scratch pad, to quikcly jot down your ideas/notes","date":"2013-10-07 22:44","description":"Edit content online in a note taking scratch pad built using javascript","category":"software development","body":"\n## Content-Editable\n\n[scratch pad](http://scratch.markgandolfo.com) is a simple text area with some local storage for quick access to notes.\n\n### How does it work?\nAs you type it'll store your text in local storage. Everyone starts with a \"scratch\" pad they can quickly jot notes down onto, but the ability to add different notes/tabs are available.\n\n### Whats to come?\n\nThere are a few new sexy inline wysiwyg's out on display, I would like in the near future to implement one of those to easily style content.\n"},{"slug":"mgwriter---a-markdown-simple-notepad","title":"mgwriter - a markdown simple notepad","date":"2016-12-28 22:44","description":"An online note taking app, written for the browser with a live markdown preview","category":"software development","body":"\n## What is it?\n\n[mg-writer](mgwriter.surge.sh) is a simple text editor for your browser. You can write in markdown and see a live preview.\n\n### How does it work?\nAs you type in the notepad, mg-writer will store your note in indexDB in your browser. Give it a try!\n"}]

