module.exports = [{"icon":"fab fa-node-js","slug":"nodejs-knockout-retrospective--dns-bot-app","title":"Node.Js Knockout Retrospective & Dns Bot App","description":"","date":"2010-08-28 06:22:31","tags":"nodejs","short_tease":"The first NodeJS Knockout","category":"development","body":"\nIt was a crazy 48 hours! My team members [@coenhyde](http://twitter.com/coenhyde) and [@nathanhoad](http://twitter.com/nathanhoad) and myself had little knowledge of node.js and really didn't know how to even start a project. But we investigated a bunch of frameworks and decided on: \n  \n[ExpressJS](http://expressjs.com/) as our main framework.\n[Jade](http://jade-lang.com/) as our template language (which was integrated into express) \n[Less](http://github.com/cloudhead/less.js.git) for our stylesheets \n[MongoDB](http://www.mongodb.org/) for our database server \n\nIt all started with a mad rush to understand what the hell was happening within node, and more importantly how we could implement our idea! The event based architecture that node is built around made for an interesting adventure since our whole team is mainly PHP and Ruby on Rails developers. The first day was spent finding our feet, given the massive amount of nodeJs plugins and frameworks, but by the end of the day we had a good idea of the scope of the technology and more importantly a good idea of how much code we could cut within the remaining 24 hours. We decided to ditch our original idea and move more towards a simple tool that every technologist has been craving! \n\nWe called it [DNS Bot App](http://dnsbotapp.com/) and its primary purpose is to assist you in the propagation of a domain. It works by pinging a variety of root servers all around the globe and building a picture of where your domain is resolving. for this first iteration it will show you A records, but stay tuned for more to come! All in all, Node Knockout was in my opinion a complete success, the goal of the competition was to get a few more people involved in Node and more importantly build some buzz around it. \n\nPersonally my goals going into the competition was simply to learn a new language, and although I'm nowhere near an expert, I really feel I've crossed that first hurdle that exists when learning a new language! So if you think that [DNS Bot App](http://dnsbotapp.com/) may be useful for you now or in the future, please show some support and [Vote for us](http://nodeknockout.com/teams/cronify)! So a big shoutout to the node.js team, node knockout crew and of course my team members. It was a cool beer filled and sleep deprived ride, but one I'm looking forward to doing again!"},{"icon":"fas fa-gem","slug":"rails-plugin-associated-with","title":"Rails Plugin: Associated With?","description":"","date":"2010-01-16 03:30:01","tags":"ruby","short_tease":"A simple gem to check if items are associated with each other","category":"development","body":"\n[Associated With](http://github.com/markgandolfo/associated_with) is a simple ruby on rails plugin that will check if an object is associated with another object. has_one and belongs_to \n    \n    \n    class Computer < ActiveRecord::Base\n      belongs_to :programmer\n    end\n    \n    class Programmer < ActiveRecord::Base\n      has_one :computer\n    end\n    \n\nSo if, Programmer with id 1, is associated with Computer 1, but not Computer 2 using associated_with? we will return \n    \n    \n    programmer = Programmer.find(1)\n    computer = Computer.find(1)\n    computer2 = Computer.find(2)\n    \n    programmer.associated_with?(computer)\n    # => true\n    \n    programmer.associated_with?(computer2)\n    # => false\n    \n\nWe can even do it the other way around \n    \n    \n    computer.associated_with?(programmer)\n    # => true\n    \n\nThis will work with all associations with ActiveRecord, for example, a has_many :through \n    \n    \n    class Analyst < ActiveRecord::Base\n      has_many :document_stores\n      has_many :documents, :through => :document_stores\n    end\n    \n    class Document < ActiveRecord::Base\n      has_many :document_stores\n      has_many :analysts, :through => :document_stores\n    end\n    \n    class DocumentStore < ActiveRecord::Base\n      belongs_to :document\n      belongs_to :analyst\n    end\n    \n\nso if document 1 is associated to analyst 1 & 2, but not 3. then \n    \n    \n    Analyst.find(1).associated_with(Document.find(1))\n    # => true\n    \n    Analyst.find(3).associated_with(Document.find(1))\n    # => false\n    \n\nCheck out the tests for more information."},{"slug":"hudson-ci-server-running-cucumber-with-xvfb","title":"Hudson Ci Server Running Cucumber with Xvfb","description":"","date":"2010-07-01 06:15:05","tags":"hudson, ruby, cucumber","short_tease":"Who needs screens to run acceptance tests?","category":"development","body":"\nI was determined to get our CI server running cucumber features in headless mode. I ran into a few webdriver problems which resulting in a small monkey patch (capybara patch being written and submitted) for capybara. This is how I got it running on a fedora 12 server (and some tips on getting it working on an ubuntu server). \n\n## Install Xvfb\n\nOn fedora \n    \n    sudo yum install xorg-x11-server-Xvfb\n\non ubuntu \n    \n    sudo apt-get install xvfb\n\n### Running Xvfb\n\nTo run Xvfb, in console simply type, then set a environment variable to bind all X requests from the command line to go to the new virtual deskspace \n    \n     \n      Xvfb -ac :99\n      export DISPLAY=:99\n    \n\nThis will launch a new virtual display bound to display 99. \n\n### Install X11VNC to see whats happening!!\n\nThis is very cool. You can actually set up a vnc server and bind it to the virtual display to see what is happening!!! On Fedora \n    \n       sudo yum install x11vnc\n\nOn ubuntu \n    \n     sudo apt-get install x11vnc>\n\n### To run\n\nSimple run the following command! And point your VNC client to the installation server! \n    \n     x11vnc -display :99 \n\nTest it by going to the command line and typing something like `xterm` or `firefox`! \n\n## Hudson\n\nInstall the hudson gem The hudson gem is in the gems repo, but for docmentation go http://github.com/cowboyd/hudson.rb. I generally do this as the user \"hudson\". \n    \n     \n      gem install term-ansicolor\n      gem install yajl-ruby\n      gem install httparty -v=0.5.2\n      gem install builder -v=2.1.2\n      gem install thor -v=0.13.6\n      gem install hpricot \n      gem install hudson --pre\n    \n\n### Install hudson\n\nOn the command line just type \n    \n     hudson server \n\nthis will install hudson, and save configuration to the ~/.hudson directory \n\n### Checkout your application to this box\n\nFirst thing is to ensure you have set up a git user and email in the global config \n    \n     \n      git config --global user.name \"hudson\"\n      git config --global user.email \"hudson@myserver.com\"\n    \n\nThen clone your app down, install all associated gems and create your testing database \n    \n     \n      git clone git@github.com:username/mygitrepo.git\n      rake gems:install\n      rake db:create:all\n      RAILS_ENV=test rake db:schema:load\n    \n\n### Test cucumber/webdriver runs\n\nType `cucumber` in your apps directory \n\n### Create a new Hudson Project\n\nThanks to the hudson gem, you can set up the inital project skeleton via the command line (isn't technology great) Here we can use the hudson create command and tell hudson to use the current directory, and that hudson is installed on localhost:3001 \n    \n     \n      cd #{to_your_rails_app}\n      hudson create . --host localhost --port 3001\n    \n\n### Set up the hudson project\n\nLog into the project, if its on your local computer, point your browser to http://localhost:3001 and click on your project, also skip this next section. If you're installing hudson on a remote server you'll need to ssh port forward since hudson at this point is only listening on localhost. \n    \n     \n      ssh -L 8123:localhost:3001 hudson@mytestserver.com\n    \n\nThen open a browser and point it to http://localhost:8123 and click on your project \n\n### Set up the build\n\n  1. Remove all defined build sets.\n  2. Add a new build step \"Execute Shell\" with the command \"cp config/database.tests.yml config/database.yml\"\n  3. Add a new build step \"Exceute Shell\" with the command \"RAILS_ENV=test rake gems:install\"\n  4. Add a new build step \"Execute Shell\" with the command \"RAILS_ENV=test rake db:migrate\"\n  5. Add a new build step \"Execute shell\" with the command \"spec spec\" # if you have specs\n  6. Add a new build step \"Execute shell\" with the command \"DISPLAY=:99 cucumber\" # with cucumber\nPress the save button. \n\n## If webdriver doesn't append to your virtual display\n\nIf webdriver doesn't append to your Xvfb then you can put this script in /features/support/capybara_webdriver_patch.rb \n    \n     \n    # Added profile support for capybara, so we can run our tests in headless mode\n    class Capybara::Driver::Selenium < Capybara::Driver::Base\n      def self.driver\n        unless @driver\n          profile = Selenium::WebDriver::Firefox::Profile.new\n          profile.load_no_focus_lib = false\n          @driver = Selenium::WebDriver.for :firefox, :profile => profile\n          at_exit do\n            @driver.quit\n          end\n        end\n        @driver\n      end\n    end\n    \n\n### Some after thoughts\n\nSome extras you should set up before you think your finished. They're outside of the scope of this document but there are plenty of docs out there on the interwebs who can give you all the information you need to rock and roll. \n\n  * At the moment its open to everyone, go to global configuration and set up ACL's\n  * Your builds aren't very continuous yet, you can schedule builds periodically by going into the project configuration area, or I believe you can tie it into githubs callback mechanisms\n\n## Comments\n\n**[Alister Scott](#506 \"2011-08-29 06:14:50\"):** There is a 'headless' gem that does all the xvfb stuff for you. Also, there is a Jenkins rake plugin which can execute rake for you automatically. See this blog post of mine for further info: http://watirmelon.com/2011/08/29/running-your-watir-webdriver-tests-in-the-cloud-for-free/\n\n"},{"icon":"fas fa-gem","slug":"rails3-with-cucumber-rspec","title":"Rails3 With Cucumber, Rspec","description":"","date":"2010-08-22 06:20:06","tags":"ruby, cucumber, rspec","short_tease":"How to set up rails 3, cucumber and rspec","category":"development","body":"\nInitially I had a few problems getting rspec and cucumber (especially gherkin) working with rails3 beta and ruby 1.9.2! Mainly these problems I ran into seemed to deal with bundler. So easy enough, to get cucumber and rspec working, add the following to your Gemfile \n    \n    \n    group :test do\n      gem 'webrat'\n      gem 'rspec'\n      gem 'capybara'\n      gem 'database_cleaner'\n      gem 'cucumber-rails'\n      gem 'cucumber'\n      gem 'rspec-rails'\n      gem 'spork'\n      gem 'launchy'\n    end\n    \n\nthen run \n    \n    bundle install\n\nchances are the first time you try to run your tests (especially if you're using rvm) you'll have segfaults displayed. To fix this manually remove the natively compiled gets and reinstall them using the gem command. i.e. \n    \n    \n    gem uninstall gherkin nokogiri \n    gem install gherkin -v=2.1.5\n    gem install nokogiri -v=1.4.3.1\n    \n\nDo this for each of your segfaulting gems and things should start to work!"},{"slug":"cups--hp-lib-on-raspberry-pi-to-create-a-new-paperless-life","title":"Cups & HP-Lib on raspberry Pi to create a new paperless life","description":"","date":"2012-12-08 02:44:30","tags":"raspberrypi","short_tease":"","ignore":"true","category":"development","body":"\nI tend to hoard paper. It's not something I like doing, but I have a hard time differentiating the important bills from the non important, so I tend to keep everything which usually piles up until it falls over and I spend a day going through it, throwing out most things. There must be a solution to push all of these documents in a digital format to one of the many services online (read dropbox/google drive). Although I'm aware doxie exists, about this time I bought a raspberry Pi, and have had a ball making LED's flash, but I couldn't help thinking that there must be a better use for it. So, I think you all know where I'm going with this. My plan: Raspberry Pi to be connected to my HP coloursmart c7280 all-in-one device (of which the printer no longer works, so it's only use is now a scanner). I'll wire up (via breadboard) a number of switches & buttons. 6 switch, dip Switch will determine document type (it'll light a specific LED - I'll have printed labels next to the LED's) The Button will initiate the process which will: \n\n  * Start the scan\n  * Rename the document (This part is still needs to be solved)\n  * Upload the document\n  * Clean up"},{"icon":"fab fa-node-js","slug":"how-to-install-node-js-and-roll-out-your-first-project","title":"How To: Install Node Js And Roll Out Your First Project","description":"","date":"2010-09-03 06:24:34","tags":"nodejs","short_tease":"Installing NodeJS on Mac","category":"development","body":"\nSo after [NodeKnockout](http://nodeknockout.com/) I have to say I learnt a lot about the simplicity that comes with developing a nodeJs app! A few people have asked me about how easy it is to roll out their own app, and I've walked a few people though it, and finally decided it warranted a blog post! You'll be walked through how to set up: \n\n  * Install [NodeJS](http://nodejs.org/)\n  * Install [Node Package Manager (npm)](http://npmjs.org/)\n  * Install the [Express](http://github.com/visionmedia/express) framework, and a few other packages\n\n### Quick Note\n\nI'm doing this on a mac, but it should be the same on any posix system (linux, bsd, etc) (sorry windows, but if you want to be a serious developer, and .Net isn't doing it for you, then change your OS) So lets get this started! \n\n## Install NodeJS\n\nVisit the [NodeJS](http://nodejs.org/) website and download the latest node package. It'll come in tar.gz format. At the time of writing this, its node-v0.2.0.tar.gz. So I'll pull it down, and install it! On the console do: \n\n```bash\nwget http://nodejs.org/dist/node-v0.2.0.tar.gz\ntar xvf node-v0.2.0.tar.gz\ncd node-v0.2.0\n./configure\nmake\nsudo make install\n```\n\nThe sudo is required as node installs /usr/local/lib/ Once thats done, check to make sure you have it installed \n\n```bash\nnode --version => 0.2.0\n```\n\n## Install NPM\n\nVisit the [NPM](http://npmjs.org/) site. There is a link to the github repo that has some extra info not on the site. I recommend visiting it and reading the readme.. After you've done that, to install: \n\n```bash\ncurl http://npmjs.org/install.sh | sudo sh\n```\n\nAnd then check if its installed \n\n```bash\nnpm --version => 0.1.27-11\n```\n\nYou might have a later version, again this was the latest at time of writing. \n\n## Express\n\nExpress is a sinatra like framework that wraps nodejs. It looks pretty sexy! It uses [Jade](http://github.com/visionmedia/jade) by default, which is a HAML like templating language. Very sexy indeed. \n\n```bash\nsudo npm install express\nsudo npm install jade\n```\n\n## Rolling out a new nodejs app!\n\nExpress has a few generators, which makes life easier for all involved. \n\n```bash\nexpress my_new_project\n```\n\nGo through the project and have a look at the directories/files. The important one there is the app.js file. This file is used to run your new project! To run your node server run \n\n```bash\nnode app.js\n```\n\nYou can then point the browser at http://localhost:3000 and bingo! \n\n### Where to find packages?\n\nThere is a good list of hosted [Addons & Modules](http://github.com/ry/node/wiki/modules). Or just a bit of googling will come up with a few extras. In further articles I'll write a little bit more about how to modify the app, add extra actions, etc."},{"icon":"fas fa-gem","slug":"rails3-undefined-method-includes-values","title":"Rails3: Undefined Method Includes Values","description":"","date":"2010-09-09 06:31:57","comments":"true","tags":"ruby","short_tease":"Are you getting undefined method include values on scopes?","category":"development","body":"\nI can across an error in Rails3 today when I was trying to implement a scope. \n\n```ruby\nscope :latest, order(\"created_at asc\").first\n```\n\nWhen trying to run that Scope I'd get `undefined method 'includes_values' for <#Product:rarrarrar>` It had me scratching my head since the trace showed me nothing. So I went through the activerecord codebase, and sure enough it was something stupid that I did. The scope should return an ActiveRecord object, and not a result set. So simply removing the .first off my scope resulting in a working scope \n\n```ruby\nscope :latest, order(\"created_at asc\")\n```\n\nIts a simple fix, but googling came up with nothing. So I hope this helps someone else who comes across this error."},{"icon":"fas fa-code-branch","slug":"git-completion-on-a-mac","title":"Git Completion On A Mac","description":"","date":"2010-06-09 13:33:17","tags":"git","short_tease":"How to get git completion working on a mac","category":"development","body":"\n# Git Completion On A Mac\n\nTo enable bash for git you need to include the git bash completion script in your profile \n    \n    \n    cd GIT_SRC/contrib/completion\n    cp git-completion.bash ~/bin\n    echo \"source ~/bin/git-completion.bash\" >> ~/.profile\n    . ~/.profile\n    \n\nOr simply enough if you have the latest git version from macports, follow this one command to automagically include the bash completion file in your profile shell script \n    \n    \n    echo \"source /opt/local/var/macports/software/git-core/1.6.5.3_0+doc/opt/local/share/doc/git-core/contrib/completion/bash_completion.bash\" >> ~/.profile\n    \n\nAnd then reload your profile \n    \n    \n    . ~/.profile\n    \n\nAnd hey presto!"},{"icon":"fas fa-gem","slug":"send-keys-released","title":"Send Keys Released!","description":"","date":"2010-06-09 05:52:46","tags":"ruby, cucmber","short_tease":"How to send keystrokes to the browser in your cucumber tests","category":"development","body":"\nSend keys is a capybara extension that lets you send keystrokes to an element in the browser. It uses webdriver so must be used using the @javascript tag in your features. Check it out at [http://github.com/markgandolfo/send-keys](http://github.com/markgandolfo/send-keys) and read about it at [http://markgandolfo.com/pages/send-keys](/pages/send-keys)\n\n### How to use it\n\nFirst make sure you use the @javascript tag, to force capybara to use the webdriver driver. Then in your features you can send characters or modifier keys to an element, or an array of modifier keys and keys. You'll need to use the css selectors to select an element. For Example \n    \n        # Send the 'a' character to the input who's id is search (#search)\n        And I send a to \"input#search\"\n        \n        # Send the 'a', 'b' and 'c' characters to the input who's id is search (#search)\n        And I send abc to \"input#search\"\n        \n        # You can put them in quotes if you feel more comfortable\n        And I send 'abc' to \"input#search\"\n        \n        # You can also send modifier/special key strokes to an element\n        And I send arrow_left to \"input#search\"\n        \n        # You can even send a combination of modifier and characters\n        # This will result in a the charcater 'A' being sent to the input\n        And I send [shift, a] to \"input#search\"\n        \n        # Or maybe you just want to press enter\n        And I send enter to \"input#search\"\n    \n        # How cool would it be to test the counter in a text area (say for a twitter app)\n        And I send hello to \"#message\"\n        And I should see \"135\" in \"characters_left\"\n        And I send backspace to \"#message\"\n        And I should see \"135\" in \"characters_left\"\n        \n        # We used it to test completion suggestions\n        # The first suggested name was highlighted and responded to an enter keypress\n        And I send \"bo\" to \"input#username\"\n        And I should see \"bob\" within \"username_suggestions\"\n        And I send enter to \"input#username\"\n    \n\nList of modifiers/special keys There are a list of modifier and special keys which can be sent to an element null, cancel, help, backspace, tab, clearreturn, enter, shift, left_shift, control, left_control alt, left_alt, pause, escape, spacepage_up, page_down, end, home, left, arrow_left, uparrow_up, right, arrow_rightdown, arrow_down, insert, delete, semicolon, equals, numpad0, numpad1, numpad2, numpad3, numpad4, numpad5, numpad6, numpad7, numpad8, numpad9, multiplyadd, separator, subtract, decimal, divide, f1, f2, f3, f4, f5, f6, f7, f8, f9, f10, f11, f12 Please note Different OS' have different ways of simulating modifier keys, as a result not all will work on MacOSX, for example tab will not work. When I have some time I'll patch webdriver to support these keys."},{"icon":"fas fa-gem","slug":"how-to-debug-rails-apps","title":"How To Debug Rails Apps","description":"","date":"2010-06-20 05:54:49","tags":"ruby","short_tease":"3 simple tips to debugging in rails","category":"development","body":"\nComing from PHP I used to rely heavily on the die() command to print out debugging information. In rails nobody could really tell me if there was an equivalent, I was told to \"raise\", which I'm sure everyone feels is a bit dirty to use. So I thought I'd quickly put up a few ways I debug in Rails. \n\n### Ruby-Debug\n\nIts a sexy little gem that lets you easily step through your code and even drop into an IRB session. To use it, make sure you have the gem or plugin installed and then. \n    \n    require 'ruby-debug'; debugger\n\nRuby debug shines when running a script/server (mongrel, webrick, etc) but can also be used with passenger, but with limitations. \n\n### Abort\n\nThis is equivelent to the PHP die() command. It takes an argument, evals and prints the statement to the screen. Note that it does halt execution. \n    \n    abort('hello world')\n\nThis is a nice hacky way to print something out to the screen very fast, no matter where you are in your application. \n\n### Debug\n\nDebug lets you easily eval something to a screen in your views. \n    \n    debug('hello world')\n\nNow that you have a bunch of debugging commands with rails, please stop using raise() to debug!"},{"icon":"fas fa-gem","slug":"capybara-rspec-swinger-and-a-dash-of-awesome","title":"Capybara, RSpec, Swinger and a dash of awesome","description":"","date":"2011-06-09 12:30:23","tags":"ruby, rspec, capybara","short_tease":"Getting your testing enviornment set up in rails 3","category":"development","body":"\nWhen trying to get Capybara-rspec working I went googling first, to see how others have done it... What I found felt complicate... Heres how I got it all working! \n\n```ruby\ngem 'rspec-rails'\ngem 'capybara'\ngem 'swinger'\n```\n\nAdd rspec rails, capybara and swinger, then install your gems and generate rspec skeleton files \n\n```\nbundle install\nrails g rspec:install\n```\n\n\nIn your spec/spec_helper.rb file require your gems.. a quick note, I force spec to use \"test\" env, why wouldn't it do this by default? I'm sure theres an answer, but it doesn't feel write, and everything works just fine like this! \n\n```ruby\n\n# spec/spec_helper.rb\nENV[\"RAILS_ENV\"] = 'test'\nrequire File.expand_path(\"../../config/environment\", __FILE__)\nrequire 'rspec/rails'\nrequire 'capybara/rspec'\nrequire 'swinger'\n\nDir[Rails.root.join(\"spec/support/**/*.rb\")].each {|f| require f}\n\nRSpec.configure do |config|\n  config.mock_with :rspec\n  config.fixture_path = \"#{::Rails.root}/spec/fixtures\"\n  config.use_transactional_fixtures = true\nend\n```\n\nI really like the idea of my integration tests living in a directory called \"spec/integration\", so I create it, and put some tests in there. \n\n```ruby\n\n# /spec/integration/user_integration.rb\nrequire \"spec_helper\"\n\ndescribe 'A user session' do\n\n  it 'should allow a user to login', :driver => :selenium do\n    visit '/'\n    click_link 'Login'\n    fill_in 'Email', :with => \"user@markyg.com\"\n    fill_in 'Password', :with => \"password\"\n    click_button 'Sign in'\n    find('Signed in successfully')\n  end\nend\n```\n\nA quick note, swinger allows us to add :driver => :selenium, or :driver => :culerity to our specs, this is how we can easily switch drivers.. if you leave this out, it'll use rack."},{"icon":"fas fa-gem","slug":"changing-the-cookie-domain-path-in-rails","title":"Changing The Cookie Domain Path In Rails","description":"","date":"2010-03-24 03:36:15","tags":"ruby","short_tease":"Do you want to change your cookie domain in rails?","category":"development","body":"\nIn rails 2.3.4+ if you want to modify your cooking information set by your system, in any of your environment files you can simple overwrite them as follows. \n    \n    \n    config.action_controller.session = {\n    :session_key => '_my_session_id',\n    :secret => '..your_secret_code_here..',\n    :domain => \".domain.com\"\n    }\n    \n\nObviously if you only want to change your domain for instance you can simple do \n    \n    config.action_controller.session[:domain] = \".domain.com\""},{"icon":"fas fa-gem","slug":"observers-in-rails","title":"Observers In Rails","description":"","date":"2010-06-21 06:13:31","tags":"ruby","short_tease":"Why you should use observers in rails","category":"development","body":"\nIn an effort to reduce the clutter of before and after callbacks in your Rails models an Observer can be used. In a simple use case, you may want to give the user 500 credits in your online store when they signup. Without an observer your User model would look something similar to \n    \n    \n    class User < ActiveRecord::Base\n       def before_create\n         self.credits = 500\n      end\n    end\n    \n\nWhich is fine, if thats all that is in your model. But once your model starts to grow its generally a good idea to keep the callbacks in a different location! Observers to the rescue! With a Rails Observer your code would look similar to \n    \n    \n    # models/user.rb\n    class User < ActiveRecord::Base\n    end\n    \n    # models/user_observer.rb\n    class UserObserver < ActiveRecord::Observer\n       def before_create(user)\n         user.credits = 500\n      end\n    end\n    \n\nNow tell Rails that you want your observer to be included \n    \n    \n    # config/environment.rb \n    Rails::Initializer.run do |config|\n      config.active_record.observers = :user_observer\n    end\n    \n\nRails will automatically understand that this observer is for the user class based on the name (convention over configuration to the rescue). Although you can also have an observer where the model can't be inferred by the name, in this case you can explicitly define the models the observer should be observing. For instance, lets say we have an Audit observer, for a number of models. \n    \n    \n      class AuditObserver < ActiveRecord::Observer\n        observe :user, :item\n    \n        def after_update(record)\n          AuditTrail.new(record, \"UPDATED\")\n        end\n      end\n    \n\nThis will observe the user model and on an observed update will create a new audit trail record. Sexy huh? So no excuses, clean your code up, use observers! Its a sexy design pattern which I feel is completely underused!"},{"icon":"fas fa-code-branch","slug":"git-tips","title":"Git Tips","description":"","date":"2010-03-16 03:33:27","tags":"git","short_tease":"A quick beginners toolbelt for git","category":"development","body":"\nI'll just post a few quick little command line git commands. **Basic Commands** Sync the meta data (e.g. branch names, tag names, etc) with remote.. This will not update the branches, just the names, etc. \n    \n    \n    git fetch\n    \n\nTo see a list of remote branches \n    \n    \n    git branch -r\n    \n\nTo see a list of local branches \n    \n    git branch\n\n# or \n    \n    git branch -l\n\nTo see both local and remote \n    \n    git branch -a\n\n**Branching** To create a new branch from the branch you are currently on. \n    \n    git checkout -b new_branch\n\nTo delete a branch \n    \n    git branch -d new_branch\n\nTo delete a remote branch \n    \n    git push origin :new_branch\n\nTo checkout into a new branch, and merge uncommitted changes \n    \n    git checkout -mb new_branch\n\nTo checkout into an existing branch, merging uncommitted changes \n    \n    git checkout -m existing_branch\n\nCheckout a remote branch (one that you haven't checked out before) \n    \n    git checkout --track -b branch_name origin/branch_name\n\n**Tags** To tag at the current point in time on the current branch \n    \n    git tag new_tag\n\nTo push tag to a remote \n    \n    git push --tags\n\nTo delete a tag (locally) \n    \n    git tag -d new_tag\n\nTo delete a tag from a remote \n    \n    git push origin :tags/new_tag"},{"slug":"wordcountrcom","title":"Wordcountr.com","description":"","date":"2010-08-27 06:21:18","short_tease":"A small project to count words in rails","ignore":"true","category":"development","body":"\nI just wrote a simple rails app to count words in a block of text!  I built it mainly to play around with rails3 but also I hated the fact there wasn't an easy way to count words in a document. So I've created this. I hope to implement some Natural Language Processing in the future to give more details about the block of text a user submits!"},{"icon":"fas fa-gem","slug":"raw-output-of-html-in-rails3","title":"Raw Output Of Html In Rails3","description":"","date":"2010-09-11 06:33:14","tags":"ruby","short_tease":"dangerouslly set html in rails","category":"development","body":"\nAs of rails3 the <%=h tag is now applied by default to all of your <%= @content So for instance in a message: \n\n```ruby\n# Controller\n@message = \"Unfortunately an error occurred: Sorry, this product is no longer available, if you think you received this in error, please [contact us](/contact/us)\"\n\n# View\n= @message\n```\n\nwould result in `Unfortunately an error occurred: Sorry, this product is no longer available, if you think you received this in error, please <a href='/contact/us'>contact us</a>` To remove htmlentities being sanitized you can use the raw() method helper \n\n```ruby\n# Controller\n@message = \"Unfortunately an error occurred: Sorry, this product is no longer available, if you think you received this in error, please [contact us](/contact/us)\"\n\n# View\n=raw @message\n```\n\nwould result in `Unfortunately an error occurred: Sorry, this product is no longer available, if you think you received this in error, please [contact us](/contact/us)` Be careful how you use this, if @message was set from user input, than they could inject some nasty javascript or flash in your page."},{"icon":"fas fa-gem","slug":"how-to-integrate-delayed-job-into-your-app","title":"How To Integrate Delayed Job Into Your App","description":"","date":"2010-01-10 03:25:59","tags":"ruby","short_tease":"A quick guide on integrating delayed job into your rails app","category":"development","body":"\nSo a quick how to integrate delayed job into your app, since i've found a lot of the tutorials out there leave out some crucial information. Firstly, I used [Tobi's delayed_job](http://github.com/tobi/delayed_job) since it comes with a few extra bits a pieces, like generators and the like. **Install the plugin** You can either install it using the rails command, or use git submodules (which i won't cover here) \n    \n    \n    script/plugin install git://github.com/tobi/delayed_job.git\n    \n\n**Generate and run the migration** We'll run the generator script to generate our migration and then migrate the database up \n    \n    \n    script/generate delayed_job_migration\n    \n\nWhich will generate a migration that looks like this. Have a read through the file. You're probably asking yourself where the object to be run is stored. Its actually stored in the \"handler\" field as a sexy yaml field. \n    \n    \n      create_table :delayed_jobs, :force => true do |table|\n        table.integer  :priority, :default => 0      # Allows some jobs to jump to the front of the queue\n        table.integer  :attempts, :default => 0      # Provides for retries, but still fail eventually.\n        table.text     :handler                      # YAML-encoded string of the object that will do work\n        table.string   :last_error                   # reason for last failure (See Note below)\n        table.datetime :run_at                       # When to run. Could be Time.now for immediately, or sometime in the future.\n        table.datetime :locked_at                    # Set when a client is working on this object\n        table.datetime :failed_at                    # Set when all retries have failed (actually, by default, the record is deleted instead)\n        table.string   :locked_by                    # Who is working on this object (if locked)\n        table.timestamps\n      end\n    \n\n**How to queue mail in your app** Lets say for example we have a AccountsMailer \n    \n    \n    class AccountMailer < ActionMailer::Base  \n      def confirm(user)\n        subject       'Your account has been created'\n        recipients    user.email\n        from          'you@email.com'\n        sent_on       Time.zone.now\n        content_type  'text/html'\n        body \"Welcome Aboard\"\n      end\n    end\n    \n\nWe would normally send an email by doing something like this in our controllers \n    \n    \n    AccountMailer.deliver_confirm(@user)\n    \n\nWith delayed job, we can simply do a \n    \n    \n    AccountMailer.send_later(:deliver_confirm,@user)\n    \n\nSo delayed job adds a send_later class method, which takes an AccountMailer class method as its first param, and then the methods params as the next. **How to send the mail** Setting up actionmailer is outside of the scope of this post. So i'm assuming you have it set up already. The jobs at this stage are being queued in your database, but we haven't started any sort of job to process the queue. To start the queue simple run \n    \n    \n    rake jobs:work\n    \n\nYou should see the queue starting to process, or at idle if its empty. This is great, but what happens if the rake task dies for some reason???? Daemons to rescue!!! **Daemonising delayed job queue processor** First install the daemons gem \n    \n    sudo gem install daemons\n\nNext create a script in your script/ directory.. I called mine delayed_job \n    \n    \n    #!/usr/bin/env ruby\n    require 'rubygems'\n    require 'daemons'\n    dir = File.expand_path(File.join(File.dirname(__FILE__), '..'))\n     \n    daemon_options = {\n      :multiple   => false,\n      :dir_mode   => :normal,\n      :dir        => File.join(dir, 'tmp', 'pids'),\n      :backtrace  => true\n    }\n     \n    Daemons.run_proc('job_runner', daemon_options) do\n      if ARGV.include?('--')\n        ARGV.slice! 0..ARGV.index('--')\n      else\n        ARGV.clear\n      end\n      \n      Dir.chdir dir\n      RAILS_ENV = ARGV.first || ENV['RAILS_ENV'] || 'development'\n      require File.join('config', 'environment')\n      \n      Delayed::Worker.new.start\n    end\n    \n\nSave that, and you can now simple do the below to start the queue. \n    \n    \n    script/delayed_job start\n    \n\nI would recommend reading up on the delayed_job and daemons extensions to understand the inner workings, but at least this is enough to get you up and running! notes You can also clear the queue by running \n    \n    \n    rake jobs:clear"},{"icon":"fas fa-gem","slug":"how-to-alias-a-class-method-in-ruby","title":"How To Alias A Class Method In Ruby","description":"","date":"2010-03-30 03:37:19","tags":"ruby","short_tease":"It's obvious once you see how it's done","category":"development","body":"\nTo alias a class method, you can't just use the alias/alias_method methods. Lets say we have a simple System Event class that has a log() method. \n    \n    \n    class SystemEvent\n      def self.log\n        // Do things in here (maybe database / file log depending on log levels)\n      end\n    end\n    \n\nIf for some reason we would want to alias log to be log_me, Then I would have to lay out my class as follows \n    \n    \n    class SystemEvent\n      def self.log\n        // Do things in here (maybe database / file log depending on log levels)\n      end\n      \n      class << self \n        alias :log_me :log\n      end\n    end"},{"slug":"curl-from-within-text-mate","title":"Curl From Within Text Mate","date":"2010-05-07 05:50:53","tags":"textmate","short_tease":"download a file from the internet into a textmate folder","category":"development","body":"\nI have created a small command for textmate that lets you curl a file from within textmate. The source is on [github](http://github.com/markgandolfo/tmbundle.curl) **Installation**\n    \n    git clone git://github.com/markgandolfo/tmbundle.curl.git\n\nNavigate to the download directory and double click on the curl.tmbundle file \n\n### How to use\n\n^ ⌥ ⌘W will pop a dialog box up, place the url in text field and press enter. The file will download and your project drawer (or project+ drawer) will refresh! **Possible Todo**\n\n  * Add progress bar for larger files\n  * Possibly download to the current open file path (maybe add a checkbox or something)\n  * Maybe add some sort of output if the url can't download for some reason (404, 500, etc.)"},{"slug":"paypal-sandbox-how-to-verify-a-bank-account","title":"Paypal Sandbox: How To Verify A Bank Account","date":"2009-12-17 23:39:39","comments":"true","tags":"paypal","short_tease":"How to create a Paypal development sandbox","category":"development","body":"\nAfter about 6 hours today of pulling my hair out and trying to verify my bank account on my sandbox, I finally stumbled across a post that helped me do this For everyone else that is having a hard time getting the bank account to be verified, the secret is the BSB number. Do they tell you this anywhere? Well maybe, but do we look, hell no! Here are some step by step instructions on getting it working! **Prerequisites**: \n\n  * Create a user account to access the Developer Central Sandbox.\n  * Log in to the Sandbox account\n  * Create a Test Account of type 'seller'.\n  * This will throw an error message on saving, but it will still create the account.\n  * Click the Test Accounts tab to see the account it created.\n**Create Confirm the email address:**\n\n  1. Create business test account (done in prerequisite above)\n  2. Log into PayPal Sandbox with test business account details created earlier.\n  3. Go to Profile Tab\n  4. Go to Email\n  5. Choose the email address and click 'Confirm'\n  6. Go back to the Sandbox Test Environment and click the Test Email tab\n  7. Open the email with subject line 'Activate Your PayPal Account'\n  8. From the email body, select the confirmation URL and paste into new browser window - it will load the PayPal site (in sandbox mode).\n  9. Log in with business account.\n  10. From the landing page, click the 'confirm email' link in the 'To Do List' box.\n  11. Click the 'click here if a link doesn't appear in the email' link.\n  12. Paste in the confirmation number from the email in step 8, and click confirm. A confirmation of success should be displayed.\n  13. Click Continue.\n**Create Bank Accounts For Test Business Account:**\n\n  1. Click the Profile tab\n  2. Click Bank Accounts link\n  3. Create a fake bank account. Test Bank. Use BSB 242-200 for Australia. Any random integer for account number, so long as it's not already being used by another PayPal account. I ended up having to mash the keypad until I found an unused number.\n  4. Confirm the bank account by clicking the 'confirm' link after the step above.\n  5. Step 17 loads another page where you need to click 'Submit'.\n  6. Step 18 loads another page where you need to click 'I Agree'.\n  7. The account should now be verified.\n  8. Click the Profile Tab again.\n  9. Click API Setup\n  10. Click Request API Credentials\n  11. Select API Signature\n  12. Click I Agree, Click Submit.\n  13. Copy the details to a safe place for later reference.\n**Developer Central Sandbox**\n\n  1. Go back to the Developer central sandbox and login with your account details created in the prerequisite section of this document (i.e., not your test business account, but your actual Developer Central Sandbox account).\n  2. The Test business account should now be verfied.\n  3. Click API Credentials tab.\n  4. The API credentials obtained in step 26 should appear.\n\n## Comments\n\n**[Liam](#11184 \"2013-01-02 12:37:36\"):** Thanks for the info! After a few hours butting heads with PayPal sandbox I finally have IPN setup thanks to your instructions.\n\n"},{"icon":"fas fa-gem","slug":"builder-pattern-with-ruby","title":"Builder Pattern With Ruby","date":"2015-09-22 21:07","comments":"true","tags":"ruby, patterns","description":"How to implement the Builder Pattern in Ruby","keywords":"ruby, patterns","short_tease":"How to implement the Builder Pattern in Ruby","category":"development","body":"\nAt times you'll need to create an object, that builds it's definition over the course of the a request.\nLets for example say we're building a list of Traits for a particular user behaviour.\n\nTo create a Builder, you'll need a class that defines at least 3 methods:\n\n1. `initialize` method which defines an array\n2. `add` method which adds a trait to an array and returns self, which allows us to chain\n3. `build` method which will add any semantic sugar and return as required, in the below case an array of symbols without nils\n\n```ruby\nclass TraitBuilder\n  def initialize\n    @traits = []\n  end\n\n  def add(trait)\n    @traits << trait\n    self\n  end\n\n  def build\n    @traits.compact\n  end\nend\n```\n\nThat way we can use it as follows:\n\n```ruby\n  trait_builder = TraitBuilder.new\n\n  # You can just add one at a time\n  trait_builder.add(:blonde_hair)\n\n  # Or chain!\n  trait_builder.add(:blue_eyes).add(:male).add(:black_shirt)\n\n  # And then build\n  trait_builder.build\n  # => [:blonde_hair, :blue_eyes, :male, :black_shirt]\n```\n"},{"icon":"fas fa-gem","slug":"active-resource-to-a-php-app","title":"Active Resource To A PHP App","date":"2009-12-17 06:49:38","date_gmt":"2009-12-17 06:49:38","comments":"true","tags":"php, rails","short_tease":"How to connect a php application to rails","category":"development","body":"\nLately I've been getting into rails, what can I say its a nice clean language. I also run a forum written in php. I want to build apps for my php (vbulletin) forum, but I didn't want to do cross site database calls. I wanted my plug ins to be as independent as possible from the main vbulletin app.. In another website I've been writing, there are three components that interconnect to each other using activeresource! Since it was so easy to implement I thought I'd give it a go cross programming language! Heres how I did it! First in PHP create your restfull program! I called mine rest.php :) I know very original! Using a sexy XML library I created for php. From this simply class, I can easily create from a select from a database my xml! \n    \n    \n    fetch_array($result);\n            $xml->openTag(\"user\");      \n    \n            foreach ($user as $tag => $value) {\n                $xml->addfield($tag, $value);\n            }\n            $xml->closeTag();\n        }\n        echo $xml->build();\n    ?>\n    \n\nSo far so good! So if we go to file.php?user=1 we should get the user with id 1.. You could add all sorts of cool things here. But this is just an example to get you guys going. Now finally ensure you have mod_rewrite working in apache! now if your php file is called rest.php, you'll need this rewrite rule to make the magic happen. Just add it to a .htaccess file, or in your vhost RewriteRule ^/rest/users/(.*) /rest.php?user=$1 In your rails app, now create a model named User, e.g. models/user.rb, and add \n    \n    \n    class User < ActiveResource::Base\n      self.site = \"http://domain.com/rest/\"\n    end\n    \n\nand don't forget your corresponding controller. This will now fetch all requests from our rest.php file. If you need any help, please drop me a line! Now for our rails app in our model if we have set up \n    \n    \n    class User < ActiveResource::Base\n      self.site = \"http://domain.com/rest/\"\n    end\n    \n\nAnd we can simply do this in our rails app! You'll see below the find method on the user model, and the resulting url it'll call. \n    \n    \n    User.find(:all)      # http://domain.com/rest/users.xml\n    User.find(1)        # http://domain.com/rest/users/1.xml\n    User.find(100)    # http://domain.com/rest/users/100.xml\n    \n\nSo if your doing an :all, the app will look for model.xml. If your doing any other call, it'll look for model/?.xml. I hope this helps when building external active resource communicators."},{"icon":"fas fa-gem","slug":"ordered-hashes-in-ruby-18","title":"Ordered Hashes In Ruby 1.8","date":"2009-12-17 03:04:11","comments":"true","post_type":"post","tags":"ruby","short_tease":"Ordered Hash in Ruby 1.8","category":"development","body":"\nSo I came across a situation where I needed an ordered hash. After doing some research and talking to the #rubyonrails guys I've found I'm far from the only one who needs this, and in Ruby 1.9 there is a OrderedHash class. But until then I had to create my own, and with the help of the guys in #rubyonrails I finally got something working \n    \n    \n    # lib/ordered_hash.rb\n    class OrderedHash < Hash\n      def initialize\n        @keys = []\n        super\n      end\n    \n      def []=(key, value) \n        @keys << key unless member?(key)\n        super\n      end\n    \n      def each\n        @keys.each {|key| yield key, self[key]}\n      end\n    \n      def delete(key)\n        @keys.delete key\n        super\n      end\n    end\n    \n\nUsage is as follows! \n    \n    \n    # Usage:\n    h = OrderedHash.new\n    h[2] = 3\n    h[\"x\"] = \"y\"\n    h.delete(3)\n    h.each {|key| p key}\n    \n\nYea its pretty simple, but I'm still pretty new at all of this, so I chalk this as another milestone!"},{"slug":"maintain-state-of-an-android-application","title":"Maintain state of an android application","date":"2013-12-15 15:15","comments":"true","tags":"android","short_tease":"Are you losing state on window changes in android?","category":"development","body":"\nI've recently been getting involved in a little bit more android application development, and I was curious on the different ways state can be maintained in an android application on screen rotation, or between activities. Here is the design pattern I've found most effective. \n\nEach android application extends from the Application, it shares various characteristics of a Singleton, but fundamentally isn't a Singleton in the traditional sense. There are various discussions on stackoverflow discussing the finer semantics of this, but we'll just accept that this works, and begin.\n\nBy extending the Application class we can store application specific information. \n\n```java\n\npublic class Game extends Application {\n  private int score = 0;\n  private boolean gameLoaded = false;\n\n  public Game() {\n    if(!gameLoaded) {\n      // Init your game, build isometric tile maps, or start to define your level\n      gameLoaded = true;\n    }\n  }\n\n  public void incrementScore() {\n    score++;\n  }\n\n  public int getScore() {\n    return score;\n  }\n}\n```\n\nThen in your Activity you can simple call getApplicationContext() which returns a game object to store state, and interact. \n\n\n```java\n\npublic class MainActivity extends Activity {\n  View activityMain;\n  TextView message;\n\n  @Override\n  protected void onCreate(Bundle savedInstanceState) {\n    super.onCreate(savedInstanceState);\n    setContentView(R.layout.activity_main);\n    Game state = ((Game) getApplicationContext());\n  }\n\n  button.setOnClickListener(new View.OnClickListener() {\n    public void onClick(View v) {\n      Game state = ((Game) getApplicationContext());\n      state.incrementScore();\n      message = (TextView) findViewById(R.id.message);\n      message.setText(state.getScore());\n    }\n  });\n}\n```\n\nThis is by far the easiest way to store state of your application which isn't re-run everytime during screen rotation (orientation changes) or loading different views. "},{"slug":"how-to-read-a-file-using-clojure","title":"How to read a file using clojure","date":"2014-02-17 18:55","comments":"true","tags":"clojure","description":"How to read a small file in via clojure and store each line in a vector","keywords":"clojure, blog, tutorial","short_tease":"How to read a small file in via clojure and store each line in a vector","category":"development","body":"\nToday I needed to read a small file into a vector so I can iterate over it line by line. Here is the small function to do this. \n\n```clojure\n(defn read-file [f]\n  \"Read in a file and return the result\"\n  (-> (slurp f)\n    (clojure.string/split #\"\\n\")))\n```\n\nAnd then to use\n\n```clojure\n(println (read-file \"resources/input.txt\"))\n```\n"},{"icon":"fas fa-gem","slug":"ruby-on-rails-in-gedit","title":"Ruby On Rails In Gedit","date":"2009-12-17 06:43:10","comments":"true","tags":"rails","short_tease":"Some gedit love for rails","category":"development","body":"\nThere are a few tutorials out there to make gedit act like textmate..I usually use textmate, but there are some occasions where it just isn't possible, so I go back to gedit while on linux. Heres how to rubify your gedit! \n\n#### First Install the plugins for gedit\n    \n    sudo apt-get install gedit-plugins\n\nNext, visit [Insane Terminology](http://robzon.aenima.pl/2007/10/ubuntu-710-rails-gedit-and.html) as he has a good explanation on how to get syntax highlighting working in gedit! I'll summarize for the impatient. If you have any installed mime packages in Override.xml remove them \n    \n    rm ~/.local/share/mime/packages/Override.xml\n\nNext we go to where the real mime packages/definitions are stored and download the mime type definitions courtesy Insane Terminology \n    \n    \n    cd /usr/share/mime/packages\n    sudo wget http://robzon.kapati.net/rails/rails.xml \n    sudo update-mime-database /usr/share/mime\n    \n\nNow to finish it up, we'll get syntax highlighting corrected for .rhtml (.html.erb) files. \n    \n    \n    cd /usr/share/gtksourceview-2.0/language-specs/\n    sudo wget http://robzon.kapati.net/rails/rhtml.lang\n    \n\nTheres a heap of plugins for gedit, search the net for them, they transform gedit from windows notepad to a real ide. One thing I will mention, I have created shortcut on my top panel (taskbar, or whatever you want to call it). I've edited the gedit properties (i.e. right click --> properties) and changed \n    \n    \n    gedit %U\n    \n\nto \n    \n    \n    gedit %U --new-window\n    \n\nThis allows multiple gedit windows"},{"slug":"switching-between-activities-in-an-android-app","title":"Switching between activities in an android app","date":"2013-12-24 14:09","comments":"true","tags":"android","description":"Switching activities in an android application","keywords":"android, activities, activity","short_tease":"how to easily switch between activities in Android","category":"development","body":"\nTo move between activities in Android development you can simply use intents. \n\n```java\n// new Intent(CurrentActivity instance, targetActivity class)\nIntent i = new Intent(MainActivity.this, ResultActivity.class);\nstartActivity(i);\n```\n\nBefore starting the activity we can even send some information around\n\n```java\nIntent i = new Intent(MainActivity.this, ResultActivity.class);\ni.putExtra(\"label\", \"A value which will be passed\");\nstartActivity(i);\n```\n\nAnd then in your ResultActivity you can fetch the information \n\n```java\nmyLabel = getIntent().getStringExtra(\"label\");\n```\n\nBut what happens if we want to do something tricky? Perhaps from the MainActivity load the ResultActivity but automatically take the user back to the MainActivity. \n\nIt's easy, you simply create a new runnable with a timeout as the last argument. In the below case, it's 2000ms (2 seconds). \n\n```java\npublic class ResultActivity extends Activity {\n\n  @Override\n  protected void onCreate(Bundle savedInstanceState) {\n    // do your usual on create stuff!\n    new Handler().postDelayed(new Runnable() {\n      @Override\n      public void run() {\n        final Intent mainIntent = new Intent(ResultActivity.this, MainActivity.class);\n        ResultActivity.this.startActivity(mainIntent);\n        ResultActivity.this.finish();\n      }\n    }, 2000);\n\n  }\n}\n```\n"},{"slug":"clojure-blog-tutorial","title":"Clojure Blog Tutorial","date":"2014-01-10 22:47","comments":"true","tags":"clojure","description":"A clojure blog tutorial using mysql, compojure and cloustach, and basic auth","keywords":"clojure, blog, tutorial","short_tease":"A clojure blog tutorial using mysql, compojure and cloustach, and basic auth","category":"development","body":"\nLets create a simple blog in clojure. I think it should\n\n* Adhere to an MVC standard\n* Have an Admin interface with basic authentication\n* be backed by MySQL\n* have Views/Templates (we'll use mustache)\n\nThe source code can be found on [Github](https://github.com/markgandolfo/clojure-blog)\n\nFirst install [Leiningen](https://github.com/technomancy/leiningen) so we can start to build a clojure app! \n\n## Create the project\n\n      $ lein new compojure blog\n\nCompojure is a nifty clojure library that provides some routing. It'll parse the path in the URL and call a function.\nThe above command will give us a basic scaffold which we can begin to build our app around. The most important directory is the /src directory, this is where we'll put all of our code. \n\nFirst lets start the server \n\n    $ lein ring server\n\nGo to [http://localhost:3000](http://localhost:3000) in your browser and make sure it works. You should see a Hello World message!\n\n## Lets get started!\n\nFirst lets run through the target state anatomy of the app \n\nThere will be a couple of files and directories we will work with to adhere to MVC, most aren't there yet, but we'll create them as we move forward\n\n`/src/blog/handler.clj`\n\nOur routes will go in handler.clj, this is also the main file that gets called when we run our server.\nIt'll look like this after the generation\n\n```clojure\n// src/blog/handler.clj\n\n(ns blog.handler\n  (:use compojure.core)\n  (:require [compojure.handler :as handler]\n            [compojure.route :as route]))\n\n(defroutes app-routes\n  (GET \"/\" [] \"Hello World\")\n  (route/resources \"/\")\n  (route/not-found \"Not Found\"))\n\n(def app\n  (handler/site app-routes))\n```\n\n\n`/src/blog/controllers/` This directory will hold all of all controllers and actions which will be called from the handler.clj routes file\n`/src/blog/models/` This directory will hold the models which will store business logic  and more importantly a gateway to external data, be it from a database, document store or a web service. \n`/resources/views/` This directory will hold our moustache views.\n\n### Routes\n\nIn the `/src/blog` directory you should see a `handler.clj` file, this is where we're going to store our routes, and we'll create a few directories where we store controllers, models and views. We'll do this a little in this tutorial. \n\nIf you open up `handler.clj` you'll find a route \n\n```clojure\n(GET \"/\" [] \"Hello World\")\n```\n\nBreaking this down, it's listening to a GET request, at path \"/\" in the URL, it's not expecting any params [] to be past through and it'll return the response \"Hello World\". You could change \"Hello World\" to be another string, reload your browser to see the change.\n\n\n### Create a posts controller & index action\n\nEach controller will respond to several RESTful actions, \"index, show, new, create, edit, update, destroy\". If coming from an MVC framework these will be very familiar, if not hang in there, it'll make sense soon. \n\nLets start by create the directory `/src/blog/controllers/` and a new file in that directory called `posts.clj'.\n\nFor now, lets start by creating a function that we'll use to respond to the root path \"/\".\n\n```clojure\n\n;; /src/blog/controllers/posts.clj\n\n(ns blog.controllers.posts)\n\n(defn index [] \"Hello, from index\")\n```\n\n#### Index action to start with\n\nNow lets tell the route \"/\" to use the newly created index action in the posts controller. \nTo do this we'll do two things in the `handler.clj`\n\n1. Import the controller so all actions/functions are available in our handler.clj file\n2. In place of the string \"Hello World\" have the \"/\" route use our new index action\n    \nBelow are the two changes:\n\n```clojure\n\n;;/src/blog/handler.clj\n(ns blog.handler\n  (:use compojure.core)\n  (:require [compojure.handler :as handler]\n            [compojure.route :as route]\n            [blog.controllers.posts :as posts-controller])) ;<-- add this line\n\n(defroutes app-routes\n  (GET \"/\" [] (posts-controller/index)) ;<-- update this line\n  (route/resources \"/\")\n  (route/not-found \"Not Found\"))\n\n(def app\n  (handler/site app-routes))\n```\n\nStart the server again and load the app, you should now see \"Hello, from index\"\n\n    $ lein ring server\n\n#### Create the template for the posts, index controller\n\nSo that was easy, with a few lines of code we've managed to render a controller with a string, although easy, it wasn't very impressive.. It would be much more impressive if we could render a template file full of html in place of our \"Hello, from index\" String. \n\nTo do this there will be four things we need to do:\n\n1. Add the clostache to the project dependencies\n2. Create the mustache template\n3. Add some render helper methods \n4. Modify the posts controller index function to render the template\n\nLets do it. \n\nIn the `project.clj` file at the root of your project you need to add the dependency for clostache. This will let lein know what dependencies to download to run the application\n\n```clojure\n\n;;/project.clj\n\n(defproject blog \"0.1.0-SNAPSHOT\"\n  :description \"FIXME: write description\"\n  :url \"http://example.com/FIXME\"\n  :dependencies [[org.clojure/clojure \"1.5.1\"]\n                 [compojure \"1.1.6\"]\n                 [de.ubercode.clostache/clostache \"1.3.1\"]]  ;<-- add this line\n  :plugins [[lein-ring \"0.8.10\"]]\n  :ring {:handler blog.handler/app}\n  :profiles\n  {:dev {:dependencies [[javax.servlet/servlet-api \"2.5\"]\n                        [ring-mock \"0.1.5\"]]}})\n```\n\nNext lets create the directory `/resources/views/posts/` where we'll store the templates and create the template `index.mustache`\n\n```html\n\n<!-- /resources/views/posts/index.mustache -->\n<html>\n    <head>\n        <title>Hello World</title>\n    </head>\n    <body>\n        <div class=\"container\">\n            <h1>Hello, {{ name }}</h1>\n        </div>\n    </body>\n</html>\n```\n\nAdd the render methods:\n\nIn order to keep the codebase DRY we should be adding these to a controller-helper file, but for the purpose of this tutorial we'll add them to the `/src/blog/controllers/posts.clj` file\n\nThe below functions will read a template and render it, beware that if the file doesn't exist it'll render a \n\n```clojure\n\n;;/src/blog/controllers/posts.clj\n\n(ns blog.controllers.posts\n  (:require\n    [clostache.parser :as clostache]))\n\n(defn read-template [template-name]\n  (slurp (clojure.java.io/resource\n    (str \"views/posts/\" template-name \".mustache\"))))\n\n(defn render-template [template-file params]\n  (clostache/render (read-template template-file) params))\n\n...\n```\n\nNow simply we'll update the index action in our posts controller, and swap out the `\"Hello, from index\"` to the below\n\n\n```clojure\n\n;; /src/blog/controllers/posts.clj\n\n...\n(defn index []\n  (render-template \"index\" {:name \"clojure developer\"}))\n```\n\nReloading the webpage you should see that our HTML has rendered with our template replacement variable \"name\"\n\n\n## But what about the data? Lets add a model\n\nHere we'll need to do a few things:\n\n1. Add the mysql dependencies in project.clj\n2. Create a db, and a posts table\n3. Create a posts model\n4. Fill it with some methods to help pull content out of a db\n5. update the index.mustache file\n6. update the posts.clj to pass to use the model\n\nMigrations are out of scope of this tutorial, so create a `blog` database with a `posts` table, the table should have the following fields:\n\n```sql\n\nid:integer\ntitle:varchar(254)\nbody:text\ncreated_at:datetime\nupdated_at:datetime\n```\n\nNow we have a database, add the following jdbc & mysql connector dependencies to our `project.clj` file\n\n```clojure\n\n;; /src/project.clj\n\n(defproject blog \"0.1.0-SNAPSHOT\"\n  :description \"FIXME: write description\"\n  :url \"http://example.com/FIXME\"\n  :dependencies [[org.clojure/clojure \"1.5.1\"]\n                 [compojure \"1.1.6\"]\n                 [org.clojure/java.jdbc \"0.3.0-alpha5\"]       ;<-- add this line\n                 [mysql/mysql-connector-java \"5.1.25\"]        ;<-- add this line\n                 [de.ubercode.clostache/clostache \"1.3.1\"]]\n  :plugins [[lein-ring \"0.8.10\"]]\n  :ring {:handler blog.handler/app}\n  :profiles\n  {:dev {:dependencies [[javax.servlet/servlet-api \"2.5\"]\n                        [ring-mock \"0.1.5\"]]}})\n```\n\n\nNext lets add the directory `/src/blog/models/` with the file `posts.clj`\n\nIn the `posts.clj` file we need to add some requires statements, the mysql-db connector and the all function. \n\n```clojure\n\n;; /src/blog/models/posts.clj\n\n(ns blog.models.posts\n  (:refer-clojure :exclude [get])\n  (:require [clojure.java.jdbc :as j]\n            [clojure.java.jdbc.sql :as s]))\n\n(def mysql-db {:subprotocol \"mysql\"\n               :subname \"//localhost:3306/blog\"\n               :user \"root\"\n               :pass \" \"\n               :zeroDateTimeBehaviour \"convertToNull\"})\n\n(def now\n  (str (java.sql.Timestamp. (System/currentTimeMillis))))\n\n(defn all []\n  (j/query mysql-db\n    (s/select * :posts)))\n```\n\nUpdate the index.mustache file to show the blog posts\n\n```html\n\n<!-- /resources/views/posts/index.mustache -->\n\n<html>\n  <head>\n    <title>Clojure Blog</title>\n  </head>\n  <body>\n    <div class=\"container\">\n      <h1>Listing Posts</h1>\n      <sections>\n      {{#posts}}\n        <h2>{{title}}</h2>\n        <datetime>{{created_at}}</datetime>\n        <p>{{body}}<p>\n      {{/posts}}\n      </sections>\n  </div>\n  </body>\n</html>\n```\n\nUpdate the `posts.clj` controller to require the posts-model and change the index action\n\n```clojure\n\n;; /src/blog/controllers/posts.clj\n\n(ns blog.controllers.posts\n  (:require\n    [clostache.parser :as clostache]\n    [blog.models.posts :as posts-model]))                       ;<-- update this list\n\n(defn read-template [template-name]\n  (slurp (clojure.java.io/resource\n    (str \"views/posts/\" template-name \".mustache\"))))\n\n(defn render-template [template-file params]\n  (clostache/render (read-template template-file) params))\n\n(defn index [] \n  (render-template \"index\" {:posts (posts-model/all)}))         ;<-- update this list\n```\n\nIn your favourite sql editor, add a few test posts. Reload the browser and you should see the posts!\n\n### Lets set up an admin controller\n\nWhat's the point of having a blog without an admin? Hang on now, we'll be going a little bit faster with less explanation. But if you've got this far I think you'll get it!\n\nWhat we'll be doing here is:\n\n1. Add an admin controller with CRUD actions\n2. Create the admin routes in a protected-routes function\n3. Add basic auth to the protected-routes \n4. Add the Admin views\n\nLets add the `/src/blog/controllers/admin/posts.clj` controller with the actions required to CRUD.\n\n```clojure\n;; /src/blog/controllers/admin/posts.clj\n\n(ns blog.controllers.admin.posts\n  (:require\n    [clostache.parser :as clostache]\n    [blog.models.posts :as posts-model]))\n\n(defn read-template [template-name]\n  (slurp (clojure.java.io/resource\n    (str \"views/admin/posts/\" template-name \".mustache\"))))\n\n(defn render-template [template-file params]\n  (clostache/render (read-template template-file) params))\n\n(defn index []\n  (render-template \"index\" {:posts (posts-model/all)}))\n\n(defn show [id]\n  (render-template \"show\" {:post (posts-model/get id)}))\n\n(defn edit [id]\n  (render-template \"edit\" {:post (posts-model/get id)}))\n\n(defn new []\n  (render-template \"new\" {}))\n```\n\nUpdating the handler.clj file to include basic auth, protected and public routes\nas well as a group of the admin/posts routes, you'll notice we ommited the create/update actions, and instead handle it in the router with the do function. This is personal choice and won't always be practical. i.e. if you need to add validation, etc. \n\n```clojure\n;; /src/blog/handler.clj\n\n(ns blog.handler\n  (:use compojure.core)\n  (:require [compojure.handler :as handler]\n            [compojure.route :as route]\n            [ring.middleware.basic-authentication :refer :all]\n            [ring.util.response :as resp]\n            [blog.models.posts :as posts-model]\n            [blog.controllers.posts :as posts-controller]\n            [blog.controllers.admin.posts :as admin-posts-controller]))\n\n(defn authenticated? [name pass]\n  (and (= name \"user\")\n       (= pass \"pass\")))\n\n(defroutes public-routes\n  (GET \"/\" [] (posts-controller/index))\n  (route/resources \"/\" ))\n\n(defroutes protected-routes\n  (GET \"/admin\" [] (admin-posts-controller/index))\n  (GET \"/admin\" [id] (admin-posts-controller/show id))\n  (GET \"/admin/posts/new\" [] (admin-posts-controller/new))\n  (POST \"/admin/posts/create\" [& params]\n    (do (posts-model/create params)\n        (resp/redirect \"/admin\")))\n  (GET \"/admin/posts/:id/edit\" [id] (admin-posts-controller/edit id))\n  (POST \"/admin/posts/:id/save\" [& params]\n    (do (posts-model/save (:id params) params)\n        (resp/redirect \"/admin\")))\n  (GET \"/admin/posts/:id/delete\" [id]\n    (do (posts-model/delete id)\n      (resp/redirect \"/admin\"))))\n\n(defroutes app-routes\n  public-routes\n  (wrap-basic-authentication protected-routes authenticated?)\n  (route/not-found \"404 Not Found\"))\n\n(def app\n  (handler/site app-routes))\n```\n\nNow lets add basic auth dependency in our `project.clj` file\n\n```clojure\n;; /blog/project.clj\n\n(defproject blog \"0.1.0-SNAPSHOT\"\n  :description \"FIXME: write description\"\n  :url \"http://example.com/FIXME\"\n  :dependencies [[org.clojure/clojure \"1.5.1\"]\n                 [compojure \"1.1.6\"]\n                 [org.clojure/java.jdbc \"0.3.0-alpha5\"]\n                 [mysql/mysql-connector-java \"5.1.25\"]\n                 [de.ubercode.clostache/clostache \"1.3.1\"]\n                 [ring-basic-authentication \"1.0.2\"]]         ;<-- add this line\n  :plugins [[lein-ring \"0.8.10\"]]\n  :ring {:handler blog.handler/app}\n  :profiles\n  {:dev {:dependencies [[javax.servlet/servlet-api \"2.5\"]\n                        [ring-mock \"0.1.5\"]]}})\n```\n\nNext we'll add the admin views. \n\n```html\n\n<!-- /resources/views/admin/posts/index.mustache -->\n\n<html>\n  <head>\n    <title>Clojure Blog</title>\n  </head>\n  <body>\n    <div class=\"container\">\n      <h1>Listing Posts</h1>\n      <a href=\"/admin/posts/new\">Add</a>\n      <sections>\n      {{#posts}}\n        <h2><a href=\"/admin/posts/{{id}}\">{{title}}</a></h2>\n        <datetime>{{created_at}}</datetime>\n        <p>{{body}}<p>\n        <p>\n          <a href=\"/admin/posts/{{id}}/edit\">Edit</a>\n          <a href=\"/admin/posts/{{id}}/delete\">Delete</a>\n        </p>\n      {{/posts}}\n      </sections>\n    </div>\n  </body>\n</html>\n```\n\n```html\n\n<!-- /resources/views/admin/posts/show.mustache -->\n<html>\n  <head>\n    <title>Clojure Blog</title>\n  </head>\n  <body>\n    <div class=\"container\">\n      <h1>Listing Post</h1>\n      <sections>\n        <h2>{{title}}</h2>\n        <datetime>{{created_at}}</datetime>\n        <p>{{body}}<p>\n        <p>\n          <a href=\"/admin/posts/{{id}}/edit\">Edit</a>\n          <a href=\"/admin/posts/{{id}}/delete\">Delete</a>\n        </p>\n      </sections>\n    </div>\n  </body>\n</html>\n```\n\n```html\n\n<!-- /resources/views/admin/posts/new.mustache -->\n<html>\n  <head>\n    <title>Clojure Blog</title>\n  </head>\n  <body>\n    <div class=\"container\">\n      <h1>Editing Post</h1>\n      <form action=\"/admin/posts/create\" method=\"post\">\n        <label for=\"title\">Title</label>\n        <input name=\"title\"/><br />\n        <label for=\"body\">Body</label>\n        <textarea name=\"body\"></textarea> <br/>\n        <input type=\"submit\"/>\n      </form>\n    </div>\n  </body>\n</html>\n```\n\n```html\n\n<!-- /resources/views/admin/posts/edit -->\n\n<html>\n  <head>\n    <title>Clojure Blog</title>\n  </head>\n  <body>\n    <div class=\"container\">\n      <h1>Editing Post</h1>\n      <form action=\"/admin/posts/{{post.id}}/save\" method=\"post\">\n        <label for=\"title\">Title</label>\n        <input name=\"title\" value=\"{{post.title}}\"/><br />\n        <label for=\"body\">Body</label>\n        <textarea name=\"body\">{{post.body}}</textarea> <br/>\n        <input type=\"submit\"/>\n      </form>\n    </div>\n  </body>\n</html>\n```\n\n### Finishing up!\n\nWe haven't documented all CRUD features to keep this post somewhat manageable. Make sure you check out the github project below for all of the source code.\n\nThere's still a lot of work to do, such as styling, rendering of partials, DB based user accounts, and importantly DRYing up the code. \n\nThe source code can be found on [Github](https://github.com/markgandolfo/clojure-blog)\n\nThanks for reading. \n\n"},{"icon":"fas fa-gem","slug":"gmail-gem--omniauth-google-oauth2-setup","title":"Gmail Gem & omniauth-google-oauth2 Setup","date":"2016-01-05 15:01","comments":"true","tags":"ruby","description":"How to set up the omniauth google oauth2 gem and the Gmail gem","keywords":"ruby, gmail, omniauth, google, oauth2","short_tease":"How to set up the omniauth google oauth2 gem and the Gmail gem","category":"development","body":"\nThere are a number of ways to access a Gmail mailbox, the way I'll describe it today is using the [Gmail gem](https://github.com/gmailgem/gmail) and [omniauth-google-oauth2 gem](https://github.com/zquestz/omniauth-google-oauth2).\n\n## Setting up google api's.\n\nThere are plenty of tutorials on how to set up google apis so I won't be going into it. But ensure you have `Contacts Api`, `Google Plus` and `Gmail Api` enabled to follow along with this post.\n\n## Code, Lets get Coding!\n\nInclude the gems\n\n```ruby\n# Gemfile\n\ngem \"gmail\"\ngem \"omniauth-google-oauth2\"\n```\n\nOmniauth requires configuration which lives in the `config/initializers/omniauth.rb`. Here we define the provider and scope of access we require. This should align with the API's we have chosen in the last screen, but it could be a subset.\n\n```ruby\n# config/initializers/omniauth.rb\n\nOmniAuth.config.logger = Rails.logger\n\nRails.application.config.middleware.use OmniAuth::Builder do\n  provider :google_oauth2, ENV['GOOGLE_CLIENT_ID'], ENV['GOOGLE_SECRET'], {\n    scope: ['https://mail.google.com/','contacts','plus.login','plus.me','userinfo.email','userinfo.profile']\n  }\nend\n```\n\nNext we'll set up a `User` model to store the information from the users.\n\n```ruby\nrails g model user provider uid name email image oauth_token oauth_refresh_token  oauth_expires_at:datetime\nrake db:migrate\n```\n\nNext we'll set up a method to create a user or load them from the database.\n\n```ruby\n# app/models/user.rb\n\nclass User < ActiveRecord::Base\n  def self.from_omniauth(auth)\n    where(provider: auth.provider, uid: auth.uid).first_or_initialize.tap do |user|\n      user.provider = auth.provider\n      user.uid = auth.uid\n      user.name = auth.info.name\n      user.email = auth.info.email\n      user.image = auth.info.image\n      user.oauth_refresh_token = auth.credentials.refresh_token if auth.credentials.refresh_token\n      user.oauth_token = auth.credentials.token\n      user.oauth_expires_at = Time.at(auth.credentials.expires_at)\n      user.save!\n    end\n  end\nend\n```\n\nNext we'll define a method to grab the `current_user`. We'll put this in our `ApplicationController`\n\n```ruby\n# app/controllers/application_controller.rb\n\nclass ApplicationController < ActionController::Base\n  protect_from_forgery with: :exception\n  helper_method :current_user\n\n  def current_user\n    @current_user ||= User.find(session[:user_id]) if session[:user_id]\n  end\nend\n```\n\nWe'll use the `SessionsController` to handle the login and logout actions for a user.\nThe `create` action will be used as the callback url we'll give to google, and the `destroy` action will log a user out.\n\n```ruby\n# app/controllers/sessions_controller.rb\n\nclass SessionsController < ApplicationController\n  def create\n    user = User.from_omniauth(env[\"omniauth.auth\"])\n    session[:user_id] = user.id\n    redirect_to root_path\n  end\n\n  def destroy\n    session[:user_id] = nil\n    redirect_to root_path\n  end\nend\n```\n\nNow that we have our controller set up, lets set up some routes.\nIn your Google Credential set up you would of been asked for Redirect URL's,\nGiven the below routes, you can use whatever domain you wish and the path `/auth/google_oauth2/callback` and `/auth/failure`.\n\n```ruby\n# config/routes.rb\n\nget 'auth/:provider/callback', to: 'sessions#create'\nget 'auth/failure', to: redirect('/')\nget 'signout', to: 'sessions#destroy', as: 'signout'\n\nresources :sessions, only: [:create, :destroy]\n```\n\nI'd recommend putting some auth stuff in a partial, and including it where it's needed.\n\n```html\n# app/views/application/_auth.html.erb\n\n<% if current_user %>\n  Signed in as <strong><%= current_user.name %></strong>!\n  <%= link_to \"Sign out\", signout_path, id: \"sign_out\" %>\n<% else %>\n  <%= link_to \"Sign in with Google\", \"/auth/google_oauth2\", id: \"sign_in\" %>\n<% end %>\n```\n\nAnd that's omniauth-google sorted. Start the app, and auth yourself so we have a token to play with.\n\n#### Gmail Gem.\n\nSo the Gmail gem can be auth'd via a number of methods like username and password, or oauth1 but we'll be using oauth2.\nIt's quite simple now to connect to a user's account!\n\nLoad up your rails console, and put this in!\n\n```ruby\nuser = User.first\nGmail.connect(:xoauth2, user.email, user.oauth_token)\n\n# You should see\n=> #<Gmail::Client0x7f8f95b6ec58 (users@emailaddress.com) connected>\n```\n\nFrom there you can utilise any of the `Gmail` gem methods. i.e.\n\n```ruby\nuser = User.first\ngmail = Gmail.connect(:xoauth2, user.email, user.oauth_token)\ngmail.inbox.count\n=> 42\n```\n\n\n\n\n\n"},{"icon":"fas fa-gem","slug":"date-amp-time-helper-with-rspec-and-capybara","title":"Date & Time Helper with rspec and capybara","date":"2013-11-17 09:15","comments":"true","tags":"ruby, capybara, testing","short_tease":"How to set date and time in capybara tests in rails","category":"development","body":"\nA set of simple helpers for you to place into ./spec/helpers/helpers.rb, I searched for a while and couldn't find an elegent solution, so I quickly whiped this up and thought I'd share incase anyone else needed a hand. \n\nThe first helps select a date from a date and time rails widget\n\n```ruby\n\ndef select_date_and_time(date, options = {})\n  field = options[:from]\n  select date.strftime('%Y'), :from => \"#{field}_1i\" #year\n  select date.strftime('%B'), :from => \"#{field}_2i\" #month\n  select date.strftime('%d'), :from => \"#{field}_3i\" #day \n  select date.strftime('%H'), :from => \"#{field}_4i\" #hour\n  select date.strftime('%M'), :from => \"#{field}_5i\" #minute\nend\n```\n\nTo use it, in your capybara rpsec tests, you can find use it by\n\n```ruby\n\nselect_date_and_time(DateTime.now, from:published_date)\n```\n\nSimilarly the following helper selects just a date\n\n```ruby\n\ndef select_date(date, options = {})\n  field = options[:from]\n  select date.strftime('%Y'), :from => \"#{field}_1i\" #year\n  select date.strftime('%B'), :from => \"#{field}_2i\" #month\n  select date.strftime('%d'), :from => \"#{field}_3i\" #day \nend  \n```\n\nAnd use it as follows in your tests\n\n```ruby\n\nselect_date(Date.today, from:published_date)\n```"},{"icon":"fas fa-gem","slug":"using-to-param-ftw","title":"Using To Param Ftw","description":"","date":"2009-12-17 06:52:38","post_type":"post","tags":"ruby","short_tease":"to_param in ruby to create nice urls","category":"development","body":"\nI have a client who draws comics. He wanted the url to be /comics/12-02-2008 (where 12-02-2008 was the date the comic was published). I tried for a long while, and finally got my routes to do :controller/:action/:published_on instead of the standard :id. I did this only for the comics controller, and all others had :controller/:action/:id. I kicked myself when I found to_param! \n    \n    class Comic < ActiveRecord::Base\n        def to_param\n            \"#{id}-#{published_on}\"\n        end \n    end\n\nThis allows me to convert the :id symbol to :published_on. Much nicer than the old crusty routes!"}]

