import React, { Component } from 'react'
import PropTypes from 'prop-types'

export class TarList extends Component {
  constructor(props) {
    super(props);
    this.state = { tags: [] }
  }
  static propTypes = { tagList: PropTypes.string }
  static defaultProps = { tagList: '' }

  componentDidMount() {
    const tags = this.props.tagList.split(',').map(tag => tag.trim())
    this.setState({ tags: tags })
  }

  render() {
    return (
      <div className="tags top_30">
        <span>Tags:</span>
        {this.state.tags.map(function (tag, index) {
          // return <a href='/'>{tag}</a> 
          return <span key={index}>{tag}</span>
        })}
      </div>
    )
  }
}

export default TarList



