import React, { Component } from 'react'
import Showdown from 'showdown';
import _ from 'lodash';

import portfolioData from 'data/portfolio.json';

const converter = new Showdown.Converter();

export default class PortfolioItem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      path: '',
      title: '',
      body: ''
    }
  }

  componentDidMount() {
    const slug = this.props.match.params.slug;
    const article = _.filter(portfolioData, {slug})

    if(Array.isArray(article) && article.length > 0) {
      this.setState({
        title: article[0].title,
        body: article[0].body
      })
    }

    window.Prism.highlightAll();
  }

  componentDidUpdate() {
    window.Prism.highlightAll();
  }

  render() {
    const body = { __html: converter.makeHtml(this.state.body) }

    return (
      <div id="content">
        <div id="portfolio-item">
          <div className="row container">
            <div className="col-12 post">
              <div className="post-content">
                <h1 className="blog-title bottom_30 top_30">{this.state.title}</h1>
                <div dangerouslySetInnerHTML={body} />
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}



